import { Component, OnInit, AfterViewInit, Input } from '@angular/core';
import {NgForm} from '@angular/forms';
import { GravityService } from '../../../gravity.service';
import { Output } from '@angular/core/src/metadata/directives';
import { EventEmitter } from '@angular/core/src/event_emitter';

@Component({
    selector: 'app-applist',
    templateUrl: './applist.component.html',
    styleUrls: ['./applist.component.scss']
})
export class applistComponent implements OnInit, AfterViewInit {
    show: boolean = false;
    right: any;
    left: any;
    btn: any;
    applistData:any;
    privateMode:boolean=false;
 
    ngAfterViewInit() {
        
    }

    constructor(private API : GravityService) {
        this.applistData = API.getApp();
    }

    ngOnInit() {
        this.right = document.getElementById('right');
        this.left = document.getElementById('left');
        this.btn = document.getElementById('btn');
    }

    publish(data){
       /* var x = confirm("Would you like to publish ad !")
        var self = this;
        if(x){           
            this.API.publishApp(data, true)
            this.applistData = this.API.getApp();
        } */
		this.API.publishApp(data, true);
        this.applistData = this.API.getApp();

    }

    unpublish(data){
        this.API.unpublishApp(data);
        this.applistData = this.API.getApp();

    }
    private interval;
    pilot(){
        var that = this;
     
    this.privateMode = true;
     var count = 0;
     var applistCount = that.applistData.length-1;
     this.interval = setInterval(function(){          
          that.API.publishApp(that.applistData[count], true);
        that.applistData = that.API.getApp();
        if(applistCount == count){
            //clearInterval(i);
            count = 0;
        } else {

        count++;
        }
      },3000)
    }

    pilotStop(){
        this.privateMode = false;
        clearInterval(this.interval);
    }

    btnClicked() {
        this.show = !this.show;
        this.btn.className =
            this.btn.className === 'fa fa-times fa-2x'
                ? 'fa fa-bars fa-2x'
                : 'fa fa-times fa-2x';
        this.left.classList.toggle('hide');
        this.right.classList.toggle('hide-div-content');
    }
}



